let getters = {
    getTarrifsTargets: state => {
        let targets = []
        state.tariffs.forEach(tarrif => {
            tarrif.targets.forEach(target => {
                if (targets.filter(item => item.id === target.id).length === 0) {
                    targets.push(target)
                }
            })
        })
        return targets
    },
    getTarrifsTargetsForTariff: state => id => {
        return state.tariffs.filter(item => item.guid === id)[0].targets
    }
}

let mutations = {
    setTariffs(state, array) {
        if (array) {
            array.forEach(item => {
                item.additional = false
            })

            state.tariffs = array
        }
    },
    setCurrentTariff(state, object) {
        state.currentTariff = object
    },
    toggleTariffAditionalInfo (state, id) {
        let needItem = {}
        let needIndex = 0
        state.tariffs.forEach((item, index) => {
            if (id === item.id) {
                needItem = item
                needItem.additional = !needItem.additional
                needIndex = index
            }
        })
        Vue.set(state.tariffs, needIndex, needItem)
    }
}

let actions = {
    async getTariffs ({ state, commit, dispatch }) {
        await axios({
            url: '/tariffs',
            method: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                let res = state.tariffs
                dispatch('setTariffCalculatorData', res)
                commit('setTariffs', res)
            }
            else {
                dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async getTariffInfo (ctx, id) {
        await axios({
            url: `/tariff/${id}`,
            method: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('setCurrentTariff', resp.data.items)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    setTariffCalculatorData ({commit}, data) {
        let minTerm = 10000
        let maxTerm = 0
        let minSum = 0
        let maxSum = 0
        data.forEach((item, index) => {
            if (index === 0) {
                minTerm = item.minTerm
                maxTerm = item.maxTerm
                minSum = item.minSum
                maxSum = item.maxSum
            } else {
                if (minTerm > item.minTerm) minTerm = item.minTerm
                if (item.maxTerm > maxTerm) maxTerm = item.maxTerm
                if (minSum > item.minSum) minSum = item.minSum
                if (item.maxSum > maxTerm) maxSum = item.maxSum
            }
        })
        //Add default fields for tarrifs filters
        commit('setCreditMinSum', minSum, { root: true })
        commit('setCreditMinTerm', minTerm, { root: true })
        commit('setCreditMaxTerm', maxTerm, { root: true })
    },
    setTariffDataForOrder ({ state, commit, dispatch }, id) {
        let tariff = state.tariffs.filter(item => item.guid === id)[0]
        commit('setCreditMinSum', tariff.minSum, { root: true })
        commit('setCreditMaxSum', tariff.maxSum, { root: true })
        commit('setCreditMinTerm', tariff.minTerm, { root: true })
        commit('setCreditMaxTerm', tariff.maxTerm, { root: true })
    }
}

export default {
    state,
    getters,
    mutations,
    actions
}
